<template>
  <v-card
      class="mx-auto"
      color="#C8CC95"
      max-width="800"
  >
    <v-card-title>
      <v-icon
          large
          left
      >
        mdi-twitter
      </v-icon>
      <span class="text-h6 font-weight-light"></span>
    </v-card-title>
    <v-row>
      <v-col cols="4" offset="1">
        <v-switch
            v-model="switch1"
            :label="`: ${switch1}`"
            @change="set_status"
        ></v-switch>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import axios from "axios";
import api from '@/api/index'

export default {
  name: "show_link",
  data: () => ({
    switch1: undefined
  }),
  mounted() {
    this.get_status()
  },
  methods: {
    get_status() {
      const url = api.syn_status
      axios.get(url)
          .then(response => {
            if (response.data.status === 'true') {
              this.switch1 = false
            } else if (response.data.status === 'false') {
              this.switch1 = true
            }
          })
    },
    set_status() {
      const url = api.set_syn + this.switch1
      axios.get(url)
    }
  },
}
</script>

