<template>
  <v-overlay
      opacity="1">
    <div class="home">
      <v-card
          class="mx-auto"
          color="#26c6da"
          dark
          elevation="5"
          max-width="800"
      >
        <v-card-title>
          <v-icon
              large
              left
          >
            mdi-twitter
          </v-icon>
          <span class="text-h6 font-weight-light"></span>
        </v-card-title>

        <v-card-text class="text-h5 font-weight-bold">
          ""
        </v-card-text>
        <v-row class="justify-center mt-5">
          <v-col class="mx-2">
            <v-text-field solo label="" light v-model="username"></v-text-field>
            <v-text-field solo label="" light v-model="password"></v-text-field>
          </v-col>
        </v-row>
        <v-row class="justify-center">
          <v-col cols="12" offset="9">
            <v-btn color="green" @click="submit_data"></v-btn>
          </v-col>
        </v-row>
      </v-card>
    </div>
    <v-snackbar
        v-model="snack"
        :timeout="3000"
        top
        :color="snackColor"
    >
      <div class="text-center">{{ snackText }}</div>
    </v-snackbar>
  </v-overlay>
</template>

<script>
// @ is an alias to /src

import axios from "axios";

export default {
  name: 'HomeView',
  data: () => ({
    snack: false,
    snackColor: '',
    snackText:'',
    username: '',
    password: '',
  }),
  methods: {
    submit_data() {
      const url = '/myadmin/login'
      const params = new URLSearchParams();
      params.append('username', this.username)
      params.append('password', this.password)
      axios.post(url, params)
          .then(response => {
            if (response.data === 'data_manage') {
              window.location.href = '/myadmin/data_manage'
              this.snack = true
              this.snackColor = 'success'
              this.snackText = ''
            } else {
              window.location.href = '/myadmin/login'
              this.snack = true
              this.snackColor = 'error'
              this.snackText = ''
            }
          })
    }
  }
}
</script>
